import css from './Profile.module.css'
import { Field } from 'redux-form';
import { maxLengthCreator } from './../../../utilities/validators/validator'
import { renderField } from './../../../Common/FormsControls/FormsControls'


let maxLength30 = maxLengthCreator(30)

export let ProfileInfoEditing = ({ user, handleSubmit,error,...props }) => {

    return (

        <div>
             <form onSubmit={handleSubmit } className={css.content}>
            <div className={css.infoEditWrapper}>
              {error && <div className={css.error}>
                {error}
                </div>}

              <div className="saveBtn">
                <button>Save</button>
              </div>
              <div className="mainInfo">
                {/* fullname */}
                <div className={`${css.editInput} ${css.fullName}`}>
                  <div className="inputLabel">
                    <b>Fullname :</b>
                  </div>
                  <div>
                    <Field
                      className={`${css.infoInput}`}
                      label='Fullname'
                      name='fullName'
                      component={renderField}
                      type="text"
                      validate={[maxLength30]}
                    />
                  </div>
                </div>
                {/* AboutMe */}
                <div className={`${css.editInput} ${css.aboutMe}`}>
                  <div className="inputLabel">
                    <b>About Me :</b>
                  </div>
                  <div>
                    <Field
                      className={`${css.infoInput}`}
                      label='About Me'
                      name='aboutMe'
                      component={renderField}
                      type="text"
                      validate={[ ]}
                    />
                  </div>
                </div>
                {/* lookingForAJob */}
                <div className={`${css.editInput} ${css.lookingJob}`}>
                  <div className="inputLabel">
                    <b>Looking For A Job :</b>
                  </div>
                  <div>
                    <Field
                      className={`${css.infoInput}`}
                      label={[]}
                      name='lookingForAJob'
                      component={renderField}
                      type="checkbox"
                      validate={[]}
                    />
                  </div>
                </div>
                {/* jobDescription */}
                <div className={`${css.editInput} ${css.jobDescription}`}>
                  <div className="inputLabel">
                    <b>Job Description :</b>
                  </div>
                  <div>
                    <Field
                      className={`${css.infoInput}`}
                      label={'Job Description'}
                      name='lookingForAJobDescription'
                      component={renderField}
                      type="textarea"
                      validate={[maxLength30]}
                    />
                  </div>
                </div>
                {/* contacts */}
                <div className={`${css.inputBlock} ${css.contact}`}>
                  <div className={css.contactBlock}>
                    {Object.keys(user.contacts).map((items,index) => {
                      return (
                        <div className={css.contact} key={index}>
                          <div className="inputLabel">
                            <b>{items}:</b>
                          </div>
                          <Field
                            className={`${css.infoInput}`}
                            label={items}
                            name={'contacts.' + items}
                            component={renderField}
                            type="text"
                            validate={[]}
                          />
                        </div>
                      )
                    })}
                  </div>
                </div>
              </div>
            </div>
          </form>
         

        </div>
       
          


    )
}