import React from "react"
import css from './FormsControls.module.css'



export function renderField({className,input, label, type, meta: { touched, error} }) {
    return < div>
            <input {...input} className={className} placeholder={label} type={type} />            
        {touched && ((error && <span className={css.error}>{error}</span>))}
    </div >
}

