import React from 'react';
import Home from './Home'
import { connect } from 'react-redux';
import { setProfileThunk, setStatusThunk, updateStatusThunk,savePhoto,saveData} from './../../State/postsReducer'
import { withAuthRedirect } from './../../hoc/withAuthRedirect/withAuthRedirect'
import { withRouter } from './../../hoc/withRouter/withRouter.js'
import { compose } from 'redux'


class HomeContainer extends React.Component {

  userUpdate = () => {
    let userId = this.props.router.params.userId;
    if (!userId) {
      userId = this.props.authorizedUserId;
    }
    this.props.setProfileThunk(userId)
    this.props.setStatusThunk(userId)
  }

  componentDidMount() {
    this.userUpdate()
  }
  componentDidUpdate(prevProps, prevState) {
    if (prevProps.router.params.userId !== this.props.router.params.userId ) {
      this.userUpdate()
    }
  }



  render() {
    return (
      <Home {...this.props} 
      status={this.props.status} 
      saveData={this.props.saveData}
      isOwner={!this.props.router.params.userId}
      updateStatusThunk={this.props.updateStatusThunk} 
      profile={this.props.profile}
      savePhoto={this.props.savePhoto}
      />
    )
  }
}

let mapStateToProps = (state) => {
  return {
    profile: state.postsPage.profile,
    status: state.postsPage.status,
    authorizedUserId: state.auth.userId
  }
}

export default compose(connect(mapStateToProps, { setProfileThunk, setStatusThunk, updateStatusThunk, savePhoto,saveData }), withAuthRedirect, withRouter)(HomeContainer)

