import { stopSubmit } from 'redux-form';
import { api } from './../api/api'

const SET_AUTH_USER_DATA = 'SET_AUTH_USER_DATA';
const SET_SECURITY_CAPTCHA = 'SET_SECURITY_CAPTCHA';

let initialState = {
    userId: null,
    login: null,
    email: null,
    isAuth: false,
    captchaUrl: null     //null, if no needed
}

export const authReducer = (state = initialState, action) => {
    switch (action.type) {
        case SET_AUTH_USER_DATA: {
            return {
                ...state,
                ...action.data,
            }
        }
        case SET_SECURITY_CAPTCHA: {
            return {
                ...state,
                captchaUrl: action.url
            }
        }
        default: return state;
    }
}
//setUserData
export const setAuthUserData = (userId, login, email, isAuth) => ({ type: SET_AUTH_USER_DATA, data: { userId, login, email, isAuth } })
export const setSecurityCaptcha = (url) => ({ type: SET_SECURITY_CAPTCHA, url })

//getUserData
export const authThunk = () => {
    return async (dispatch) => {
        let data = await api.auth()
        if (data.resultCode === 0) {
            let { id, login, email } = data.data
            dispatch(setAuthUserData(id, login, email, true))
        }
    }

}

export const login = (email, password, rememberMe,captcha) => {
    return async (dispatch) => {
        let data = await api.login(email, password, rememberMe,captcha)
        if (data.resultCode === 0) {
            dispatch(authThunk())
        } 
        else {
            if(data.resultCode === 10){
                dispatch(securityCaptcha())
            }
            dispatch(stopSubmit('login', { _error: data.messages }))
        }
    }
}

export const logout = () => {
    return async (dispatch) => {
        let data = await api.logout()

        if (data.resultCode === 0) {
            dispatch(setAuthUserData(null, null, null, false))
        }
    }
}

export const securityCaptcha = () => {
    return async (dispatch) => {
        let data = await api.securityCaptcha()
        dispatch(setSecurityCaptcha(data))
    }
}


