import { api } from './../api/api'
import { stopSubmit } from 'redux-form';

const ADD_POST = 'project/posts/ADD_POST';
const SET_PROFILE = 'project/posts/SET_PROFILE';
const SET_STATUS = 'project/posts/SET_STATUS'
const DELETE_POST = 'project/posts/DELETE_POST'
const SAVE_PHOTO_SUCCESS = 'project/posts/SAVE_PHOTO_SUCCESS'

let initialState = {
    postsData: [
        { post: 'Help me to solve this problem!', likes: 32, id: 1 },
        { post: 'I cant open it', likes: 84, id: 2 },
        { post: 'Like me', likes: 23, id: 3 },
        { post: 'Who do I trust? Me!', likes: 0, id: 4 }
    ],
    profile: null,
    status: '',
}
export const postsReducer = (state = initialState, action) => {
    switch (action.type) {
        case ADD_POST: {
            let newPost = {
                post: action.newPost,
                likes: 0,
                id: 5
            };
            return {
                ...state,
                postsData: [...state.postsData, newPost],
            }
            // let newState = {...state}
            // newState.postsData = [...state.postsData]
            // newState.postsData.push(newPost)
            // newState.text = ''
            // return newState
        }
        case SET_PROFILE: {
            return {
                ...state,
                profile: action.profile
            }

        }
        case SET_STATUS: {
            return {
                ...state,
                status: action.status
            }

        }
        case DELETE_POST: {
            return {
                ...state,
                postsData: state.postsData.filter(post => {
                    return post.id !== action.postId
                })
            }
        }
        case SAVE_PHOTO_SUCCESS: {
            
            return {
                ...state,
                profile: { ...state.profile, photos: action.photos }
            }
        }

        default: return state;
    }
}



export const savePhoto = (photos) => {
    return async (dispatch) => {
        let data = await api.savePhoto(photos)


        if (data.data.resultCode === 0) {
            console.log(data.data.data.photos)
            dispatch(savePhotoSuccess(data.data.data.photos))      
        }


    }
}


export const addPost = (newPost) => ({ type: ADD_POST, newPost })
export const setProfileAC = (profile) => ({ type: SET_PROFILE, profile })
export const setStatusAC = (status) => ({ type: SET_STATUS, status })
export const deletePost = (postId) => ({ type: DELETE_POST, postId })
export const savePhotoSuccess = (photos) => ({ type: SAVE_PHOTO_SUCCESS, photos })



////////THUNK
export const setProfileThunk = (userId) => {
    return async (dispatch) => {
        let data = await api.setProfile(userId)
        dispatch(setProfileAC(data));
    }
}

export const setStatusThunk = (userId) => {
    return async (dispatch) => {
        let data = await api.getStatus(userId)
        dispatch(setStatusAC(data));

    }
}

export const updateStatusThunk = (status) => {
    return async (dispatch) => {
            let data = await api.updateStatus(status)
            if (data.resultCode === 0) {
                dispatch(setStatusAC(status));
            }   
    }
}

export const saveData = (data) => {
    return async (dispatch,getState) => {
        // api.saveData() returns a Promise
        let response = await api.saveData(data)
        // Getting User Id
        let userId = getState().auth.userId

        if (response.resultCode === 0) {
            dispatch(setProfileThunk(userId));
        }else {
            dispatch(stopSubmit('profileInfo', {_error: response.messages[0] }))
            // return Promise.reject()
        }
    }
}









