import css from './Profile.module.css'
import React, { useState } from 'react';
import Preloader from './../../Preloader/Preloader'
import ProfileStatus from './ProfileStatus'
import userDefault from './../../../assets/img/userDefault.jpg'
import { reduxForm } from 'redux-form';
import {ProfileInfoEditing} from './ProfileInfoEditing'


let Contact = ({ contactTitle, contactValue }) => {
  return <div><b>{contactTitle}</b>: {contactValue ? contactValue : 'N/A'}</div>
}

let ProfileInfo = ({ user, goToEditMode, isOwner }) => {
  return (

    <div className={css.profileInfo}>
      {isOwner && <button onClick={() => goToEditMode()}>Edit</button>}
      <div className='FullName'>
        <div>
          <b>FullName</b> : {user.fullName ? user.fullName : `N/A`}
        </div>
      </div>

      <div className='AboutMe'>
        <div>
          <b>About Me</b> : {user.aboutMe ? user.aboutMe : `N/A`}
        </div>
      </div>

      <div className='LookingForAJob'>
        <div>
          <b>Looking For A Job</b> : {user.lookingForAJob ? 'Yes' : 'No'}
        </div>

        {user.lookingForAJob &&
          <div className='AboutJob'>
            <div>
              <b>About Job</b> : {user.lookingForAJobDescription ? user.lookingForAJobDescription : `N/A`}
            </div>
          </div>
        }
      </div>

      <div className={css.contacts}>
        <div className={css.contactItems}>

          <b>Contacts</b> :{
            Object.keys(user.contacts).map((keys, index) => <Contact key={index} contactTitle={keys} contactValue={user.contacts[keys]} />)
          }
        </div>

      </div>
    </div>
  )
}




function Profile({ user, updateStatusThunk, status, savePhoto,saveData, ...props }) {
  const [editMode, setEditMode] = useState(false);
  
  let onSubmit = (data) => {

    saveData(data).then(() => {
      setEditMode(false)
    })  
    
    
  }
  if (!user) {
    return <Preloader />
  }

  let onMainPhotoSelected = (e) => {
    if (e.target.files.length) {
      savePhoto(e.target.files[0])
    }
  }

  let goToEditMode = () => {
    setEditMode(true)
  }

  
  return (
    <div className={css.home}>
      <div className={css.aboutProfile}>

        <div className={css.avatarWrapper}>
          <img src={user.photos.large || userDefault} alt="" className={css.avatar} />
          {props.isOwner ? <input className={css.changeAvatar} type='file' onChange={onMainPhotoSelected} /> : null}
        </div>
        {editMode ? <ProfileInfoEditRedux initialValues={user} onSubmit={onSubmit} user={user} /> : <ProfileInfo isOwner={props.isOwner} user={user} goToEditMode={goToEditMode} />}
        
        <ProfileStatus updateStatusThunk={updateStatusThunk} status={status} />
      </div>
    </div>

  );
}

let ProfileInfoEditRedux = reduxForm({ form: 'profileInfo' })(ProfileInfoEditing)

export default Profile;